import React from 'react';
import {useLayoutEffect,  useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';
 
const BGColorChange = ({ scrHeight, colorTo, speId }) => {

    const bgRef = useRef(); 
    const [pressed, setPressed] = useState(false);
    const [hover, setHover] = useState(false);
    const bgcolorChangeView = {
        width: "100vw",
        height: `${scrHeight}` + "vh",

    }
    const fullBG = {
        width: "100vw",
        height: "100vh",
        position: "fixed",
        top: "0vh",
        zIndex: "-1",

    }
    const btnImg = {
        width: "130px",
        height: "67px"
    }
    function changeimg() {
        setPressed(!pressed);
    }
    function onHover() {
        setHover(true);
    }
    function onHoverLeave() {
        setHover(false);
    }
    const el = useRef();
    const q = gsap.utils.selector(el);


    useLayoutEffect(() => {
        const ctx = gsap.context((self) => {
            gsap.timeline({
                scrollTrigger: {
                    trigger: bgRef.current,
                    start: "top center" ,   // trigger "left : trigger start position right: scroll postion "
                    end: "bottom top",
                     
                    //invalidateOnRefresh : true,  // color change error on refresh 
                    //    refreshPriority: 1,
                    //markers: true,   
                    scrub: 1,
                    // pin: true, //  causing pinning problem (moves after end ) 
                }
            }).to(q("#fullbgChange" + `${speId}`), { //  + `${speId}`
                backgroundColor: "#" + `${colorTo}`,
            });
        }, bgRef); // <- Scope!
        return () => ctx.revert(); // <- Cleanup!
      }, []);
    
    return (
        <>
            
            <div ref={el}>
                <div ref = {bgRef} style={bgcolorChangeView}> </div>
                <div id={"fullbgChange" + `${speId}`} style={fullBG} > </div>
            </div>
        </>

    )
}

export default BGColorChange