import React from 'react'

const AutoVideoPlay = ({vidsrc, vidwidth, vidheight, brightness, loop}) => {
    const introStyle ={
        display: "flex",
        flexDirection: "column",
        alignItems:"center",
        height:  vidheight + "vh",
        overflow: "hidden",
        zIndex: "0",
        width: vidwidth + "vw",
        maxWidth:"100%",
        top: "0",
       
    }
    const videoStyle ={
        height: "100%", 
        width: "100%",
        
        maxWidth: "1800px",
        objectFit:"cover" ,
        overflow: "hidden",
        zIndex: "0",       
        filter: "brightness("+ brightness + "%)",
        borderRadius: "50px",
    }
    return (
        <>
            <div style={introStyle} >
                <video muted playsInline autoPlay id="video" loop={loop} type="video/mp4" style={videoStyle} src={vidsrc}></video>
            </div>
        </>
    )
}

export default AutoVideoPlay