import React from 'react'
import VideoScroll from './SubComponents/VideoScroll';
import Spacer from './Spacer';
import AutoVideoPlay from './SubComponents/AutoVideoPlay';
import TextScroll from './SubComponents/TextScroll';
import BGColorChange from './SubComponents/BGColorChange';
import CardView from './SubComponents/CardView';
// css 
import Template1Css from "./ComponentsCss/Template1Css.module.css";
// videos
import ysyvid1 from '../videos/ysy.mp4';
import ysyvdi2 from '../videos/ysy_1.mp4';
const template1 = () => {
  return (
    <div className={`${Template1Css.MainContainer}`}>
      
      <TextScroll/>
      
      <Spacer marginTopVh={200} />
      < BGColorChange scrHeight={50} colorTo={"F0F4EF"} speId={"startColorCh"} />
      <Spacer marginTopVh={20} />
      <CardView />
      <Spacer marginTopVh={300} />
    </div>
  )
}

export default template1