import React from 'react';
import { useLayoutEffect, useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/all';
// window size cal 
import useWindowDimensions from '../getWindowsDimensions.js';
// css 
import TextScrollCss from "../ComponentsCss/TextScrollCss.module.css";
// auto video play 
import AutoVideoPlay from './AutoVideoPlay.js';
// vid
import ysyvdi2 from '../../videos/ysy_1.mp4';
import ysyvdi1 from '../../videos/ysy.mp4';
const TextScroll = () => {

    const el = useRef();

    const textContainerRef = useRef();
    const q = gsap.utils.selector(el);
    const { height, width } = useWindowDimensions();

    useLayoutEffect(() => {
        const ctx = gsap.context((self) => {
            const panels = self.selector(q("#TextContainer"));
            panels.forEach((section) => {
                gsap.timeline({
                    scrollTrigger: {
                        trigger: q("#TextContainer"),
                        start: "center center",
                        // makes the height of the scrolling (while pinning) match the width, thus the speed remains constant (vertical/horizontal)
                        end: () => "+=" + section.offsetHeight * 2, //+ section.offsetWidth,// + section.offsetWidth, 
                        scrub: 1,
                        snap: 0.33 ,
                        //markers: true, 
                        anticipatePin: 1
                    },
                    defaults: { ease: "none" }
                }).to(q("#TextContainer"), { 
                    y: () => -(section.offsetHeight) + "px",
                    
                }) ; 
                    
                gsap.timeline({
                    scrollTrigger: {
                        trigger: q("#passContainer"),
                        start: "center center",
                        // makes the height of the scrolling (while pinning) match the width, thus the speed remains constant (vertical/horizontal)
                        end: () => "+=250%"  , //+ section.offsetWidth,// + section.offsetWidth, 
                        scrub: 1,
                        //markers: true, 
                        anticipatePin: 1
                    },
                     
                }).to(q("#passContainer"), {
                        ease: "power4.out" ,
                        scale: 0.7
                    })
                    .to(q("#passContainer"), {
                        y: () => "-100%",
                         
                    }, 1); // ,0 here makes it move together ; // ,0 here makes it move together 
            });
        }, textContainerRef); // <- Scope!
        return () => ctx.revert(); // <- Cleanup!
    }, []);

    return (
        <>

            <div ref={el} className={`${TextScrollCss.elCont} `}>

                <div ref={textContainerRef} id="TextContainer" className={`${TextScrollCss.AllContentContainer}`}  >
                    <div className={`${TextScrollCss.TextContainer} ${TextScrollCss.center}`}>  </div>
                    <div className={`${TextScrollCss.TextContainer} ${TextScrollCss.center}`}>  </div>
                    <div className={`${TextScrollCss.TextContainer} ${TextScrollCss.center}`}>2 </div>
                    <div className={`${TextScrollCss.TextContainer} ${TextScrollCss.center}`}></div>
                </ div>
                <div id="passContainer" className={`${TextScrollCss.PassContainer}`} >
                    <AutoVideoPlay vidsrc={ysyvdi2} vidwidth={100} vidheight={100} brightness={70} loop={false} />
                </div>


            </div>

        </>
    )
}

export default TextScroll